@import 'mixins';
$hdr-banner-height: 6%;
/* global */
  button.navbar-toggler{
  height: 100%;
  width: 100%;
  background: $idf-orange url('../../images/x.svg') no-repeat center center;
  border-radius: 0px;
  color: #fff;
  &.collapsed{
    background-image:  url('../../images/hamburger.svg')
  }
  &:after{
    contet: "";
    display: inline-block;
    padding-bottom: none;
  }
}
header.banner{
width: 100%;
  .container{
  @include flexbox();
    width: 100%;
  height: $hdr-banner-height;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content:  space-between;
  align-items: baseline;
  }
    .logo{
   width: ($hdr-banner-height * 3);
    height: auto;
    order: 0;
    flex-grow: 3;
    flex-basis: 20%;
    margin: auto;
    img{
      margin-top: 1vw;
      width: 100%;
    }
  }
  nav.quick-nav{
        order: 1;
    flex-grow: 4;
    flex-basis: 70%;
    margin: auto;
    ul.quicknav{
    li{
      list-style-type: none;
      padding: 0;
      text-align: center;
      @include typo(1.2rem, 2rem, 800, #000);
      letter-spacing: 2px;
      text-transform: uppercase;
    }
  }
  }
    nav.nav-primary{
    .navbar-toggler{
    width: $hdr-banner-height;
    height: 100%;
    order: 2;
    flex-grow: 1;
    flex-basis: 10%;
    margin: auto;
    }
  }
}



